import dedent from 'dedent';
import _ from 'lodash';
import * as z from 'zod';

const Prompt_normal = z
  .strictObject({
    name: z.coerce.string(),
    id: z.never().optional(),
    enabled: z.boolean(),

    position: z
      .strictObject({
        type: z.enum(['relative', 'in_chat']),
        depth: z.number().optional(),
        order: z.number().optional(),
      })
      .prefault({ type: 'relative' })
      .superRefine((data, context) => {
        if (data.type === 'in_chat' && (data.depth === undefined || data.order === undefined)) {
          context.addIssue({
            code: 'custom',
            path: ['position'],
            message: '`in_chat`, `depth``order`',
          });
        }
      })
      .describe(': `relative` , `in_chat` '),

    role: z.enum(['system', 'user', 'assistant']).prefault('system'),
    content: z.coerce.string().optional().describe(''),
    file: z.string().optional().describe(''),

    extra: z.record(z.string(), z.any()).optional().describe(': '),
  })
  .superRefine((data, context) => {
    if (data.content === undefined && data.file === undefined) {
      ['content', 'file'].forEach(key =>
        context.addIssue({
          code: 'custom',
          path: [key],
          message: ' `content`  `file`',
        }),
      );
    }
    if (data.content !== undefined && data.file !== undefined) {
      ['content', 'file'].forEach(key =>
        context.addIssue({
          code: 'custom',
          path: [key],
          message: ' `content`  `file`',
        }),
      );
    }
  })
  .transform(data => ({
    ...data,
    id: _.uniqueId(),
  }))
  .describe('');

export const prompt_rolable_placeholder_ids = <const>[
  'world_info_before',
  'persona_description',
  'char_description',
  'char_personality',
  'scenario',
  'world_info_after',
];
export const prompt_unrolable_placeholder_ids = <const>['dialogue_examples', 'chat_history'];
export const prompt_placeholder_ids = <const>[...prompt_rolable_placeholder_ids, ...prompt_unrolable_placeholder_ids];
const Prompt_placeholder = z
  .strictObject({
    name: z.never().optional(),
    id: z.enum(prompt_placeholder_ids).describe(
      dedent(`
        , 
        - world_info_before: 
        - persona_description: .  user 
        - char_description: . 
        - char_personality: . , 
        - scenario: . , 
        - world_info_after: 
        - dialogue_examples: . , 
        - chat_history: 
      `),
    ),
    enabled: z.boolean(),

    position: z
      .strictObject({
        type: z.enum(['relative', 'in_chat']),
        depth: z.number().optional(),
        order: z.number().optional(),
      })
      .prefault({ type: 'relative' })
      .superRefine((data, context) => {
        if (data?.type === 'in_chat' && (data.depth === undefined || data.order === undefined)) {
          context.addIssue({
            code: 'custom',
            path: ['position'],
            message: '`in_chat`, `depth``order`',
          });
        }
      })
      .describe(': `relative` , `in_chat` '),

    role: z.enum(['system', 'user', 'assistant']).optional(),
    content: z.never().optional(),
    file: z.never().optional(),

    extra: z.record(z.string(), z.any()).optional().describe(': '),
  })
  .superRefine((data, context) => {
    if (_.includes(prompt_unrolable_placeholder_ids, data.id) && data.role !== undefined) {
      context.addIssue({
        code: 'custom',
        message: ` '${data.id}'  (\`role\`)`,
        path: ['role'],
      });
    }
  })
  .transform(data => ({
    ...data,
    role: data.role ?? 'system',
    name: (
      {
        world_info_before: 'World Info (before) - ',
        persona_description: 'Persona Description - ',
        char_description: 'Char Description - ',
        char_personality: 'Char Personality - ',
        scenario: 'Scenario - ',
        world_info_after: 'World Info (after) - ',
        dialogue_examples: 'Chat Examples - ',
        chat_history: 'Chat History - ',
      } as const
    )[data.id as (typeof prompt_placeholder_ids)[number]],
  }))
  .describe(', ');

export const PromptLeaf = z.union([Prompt_normal, Prompt_placeholder]);
export type PromptLeaf = z.infer<typeof PromptLeaf>;
const PromptBranch = z.object({
  folder: z.string(),
  get entries() {
    return z.array(z.union([PromptLeaf, PromptBranch]));
  },
});
const PromptTree = z.union([PromptLeaf, PromptBranch]);
function is_prompt_branch(data: z.infer<typeof PromptTree>): data is z.infer<typeof PromptBranch> {
  return _.has(data, 'folder');
}
function flatten_tree(data: z.infer<typeof PromptTree>): z.infer<typeof PromptLeaf>[] {
  if (is_prompt_branch(data)) {
    return data.entries.flatMap(flatten_tree);
  }
  return [data];
}
const PromptTrees = z.array(PromptTree).transform(data => data.flatMap(flatten_tree));

export type Preset = z.infer<typeof Preset>;
export const Preset = z.strictObject({
  settings: z.strictObject({
    max_context: z
      .number()
      .min(0)
      .max(2000000)
      .describe(
        ' token .  token ,  token ,  2000000',
      ),
    max_completion_tokens: z.number().min(0).describe(' token '),
    reply_count: z.number().min(1).prefault(1).describe(''),

    should_stream: z.boolean().describe(''),

    temperature: z.number().min(0).max(2).describe(''),
    frequency_penalty: z.number().min(-2).max(2).describe(''),
    presence_penalty: z.number().min(-2).max(2).describe(''),
    top_p: z.number().min(0).max(1),
    repetition_penalty: z.number().min(1).max(2).prefault(1).describe(''),
    min_p: z.number().min(0).max(1).prefault(0),
    top_k: z.number().min(0).max(500).prefault(0),
    top_a: z.number().min(0).max(1).prefault(0),

    seed: z.number().prefault(-1).describe(', -1 '),

    squash_system_messages: z.boolean().describe(': '),

    reasoning_effort: z
      .enum(['auto', 'min', 'low', 'medium', 'high', 'max'])
      .describe(', . ,  gemini-2.5-flash,  `min` '),
    request_thoughts: z
      .boolean()
      .prefault(true)
      .describe(
        ': ; , ',
      ),
    request_images: z.boolean().prefault(true).describe(': '),
    enable_function_calling: z
      .boolean()
      .prefault(true)
      .describe(': ;  cursor '),
    enable_web_search: z.boolean().prefault(true).describe(': '),

    allow_sending_images: z
      .enum(['disabled', 'auto', 'low', 'high'])
      .prefault('auto')
      .describe(''),
    allow_sending_videos: z.boolean().prefault(true).describe(''),

    character_name_prefix: z
      .enum(['none', 'default', 'content', 'completion'])
      .prefault('none')
      .describe(
        dedent(`
        : , 
        - none: 
        - default: ,  \`content\`  ( \`: \`)
        - content: ,  \`content\`  ( \`: \`)
        - completion: ,  \`name\` ; ,  ClaudeGoogle 
      `),
      ),

    wrap_user_messages_in_quotes: z
      .boolean()
      .prefault(false)
      .describe(': , '),
  }),

  anchors: z.any().optional().describe(' YAML , '),

  prompts: PromptTrees.superRefine((data, context) => {
    const duplicate_ids = _(data)
      .filter(prompt => _.includes(prompt_placeholder_ids, prompt.id))
      .groupBy('id')
      .filter(group => group.length > 1)
      .keys()
      .value();
    if (duplicate_ids.length > 0) {
      context.addIssue({
        code: 'custom',
        message: ` id: ${duplicate_ids.join(', ')}`,
      });
    }

    const unused_ids = _.reject(prompt_placeholder_ids, id => data.some(prompt => _.get(prompt, 'id') === id));
    if (unused_ids.length > 0) {
      context.addIssue({
        code: 'custom',
        message: ` id: ${unused_ids.join(', ')}`,
      });
    }
  }).describe(''),
  prompts_unused: PromptTrees.describe(', '),

  extensions: z.record(z.string(), z.any()).optional().describe(': '),
});
