import dedent from 'dedent';
import * as z from 'zod';

export type Worldbook_entry = z.infer<typeof Worldbook_entry>;
export const Worldbook_entry = z
  .strictObject({
    name: z.coerce.string(),
    uid: z.number().optional().describe(', '),
    enabled: z.boolean(),

    strategy: z
      .strictObject({
        type: z.enum(['constant', 'selective', 'vectorized']).describe(
          dedent(`
          :
          - constant: , .  """%" .
          - selective: , . ,  \`keys\` 
          - vectorized: . 
        `),
        ),
        keys: z
          .array(z.string())
          .min(1)
          .optional()
          .describe(': '),
        keys_secondary: z
          .strictObject({
            logic: z.enum(['and_any', 'and_all', 'not_all', 'not_any']).describe(
              dedent(`
              :
              - and_any: 
              - and_all: 
              - not_all: 
              - not_any: 
            `),
            ),
            keys: z.array(z.string()).min(1),
          })
          .optional()
          .describe(
            ': ,  `keys` ,  `logic`  `keys`',
          ),
        scan_depth: z
          .union([z.literal('same_as_global'), z.number().min(1)])
          .optional()
          .describe(': 1 , 2 , '),
      })
      .describe(': '),

    position: z
      .strictObject({
        type: z
          .enum([
            'before_character_definition',
            'after_character_definition',
            'before_example_messages',
            'after_example_messages',
            'before_author_note',
            'after_author_note',
            'at_depth',
          ])
          .describe(
            dedent(`
            :
            - before_character_definition: 
            - after_character_definition: 
            - before_example_messages: 
            - after_example_messages: 
            - before_author_note: 
            - after_author_note: 
            - at_depth: 
          `),
          ),
        role: z
          .enum(['system', 'assistant', 'user'])
          .optional()
          .describe(",  `'at_depth'` "),
        depth: z.number().optional().describe(",  `'at_depth'` "),
        order: z.number(),
      })
      .describe(': ')
      .superRefine((data, context) => {
        if (data.type === 'at_depth') {
          if (data.role === undefined) {
            context.addIssue({
              code: 'custom',
              path: ['role'],
              message: " (`position`)  `'at_depth'` ,  `role`",
            });
          }
          if (data.depth === undefined) {
            context.addIssue({
              code: 'custom',
              path: ['depth'],
              message: " (`position`) `'at_depth'` ,  `depth`",
            });
          }
        } else {
          if (data.role !== undefined) {
            context.addIssue({
              code: 'custom',
              path: ['role'],
              message: " (`position`)  `'at_depth'` , `role` , ",
            });
          }
          if (data.depth !== undefined) {
            context.addIssue({
              code: 'custom',
              path: ['depth'],
              message: " (`position`)  `'at_depth'` , `depth` , ",
            });
          }
        }
      }),

    probability: z.number().min(0).max(100).optional(),

    recursion: z
      .strictObject({
        prevent_incoming: z.boolean().describe(''),
        prevent_outgoing: z.boolean().describe(''),
        delay_until: z.number().min(1).nullable().describe(' n '),
      })
      .partial()
      .optional()
      .describe(', '),

    effect: z
      .strictObject({
        sticky: z
          .number()
          .min(1)
          .nullable()
          .describe(': ,  n , %'),
        cooldown: z.number().min(1).nullable().describe(': ,  n '),
        delay: z.number().min(1).nullable().describe(':  n , '),
      })
      .partial()
      .optional(),

    group: z
      .strictObject({
        labels: z.array(z.string()).min(1).describe(''),
        use_priority: z.boolean().default(false).describe(''),
        weight: z.number().default(100).describe(''),
        use_scoring: z
          .union([z.boolean(), z.literal('same_as_global')])
          .default('same_as_global')
          .transform(data => (data === 'same_as_global' ? null : data))
          .describe(''),
      })
      .optional()
      .describe(''),

    extra: z.record(z.string(), z.any()).optional().describe(': '),

    content: z.coerce.string().optional().describe(''),
    file: z.string().optional().describe(''),
  })
  .transform(data => {
    if (data.group !== undefined) {
      _.set(data, 'groupOverride', data.group.use_priority);
      _.set(data, 'groupWeight', data.group.weight);
      _.set(data, 'useGroupScoring', data.group.use_scoring);
      _.set(data, 'group', data.group.labels.join(','));
    }
    return data;
  })
  .superRefine((data, context) => {
    if (data.enabled && data.strategy.type === 'selective' && data.strategy.keys === undefined) {
      context.addIssue({
        code: 'custom',
        path: ['strategy', 'keys'],
        message: " (`'enabled'`)  (`'selective'`) , `keys` ",
      });
    }
  })
  .superRefine((data, context) => {
    if (data.content === undefined && data.file === undefined) {
      ['content', 'file'].forEach(key =>
        context.addIssue({
          code: 'custom',
          path: [key],
          message: ' `content`  `file`',
        }),
      );
    }
    if (data.content !== undefined && data.file !== undefined) {
      ['content', 'file'].forEach(key =>
        context.addIssue({
          code: 'custom',
          path: [key],
          message: ' `content`  `file`',
        }),
      );
    }
  });

const Wolrdbook_leaf = Worldbook_entry;
const Wolrdbook_branch = z.object({
  folder: z.string(),
  entries: z.array(Wolrdbook_leaf),
});
const Wolrdbook_tree = z.union([Wolrdbook_leaf, Wolrdbook_branch]);
function is_worldbook_branch(data: z.infer<typeof Wolrdbook_tree>): data is z.infer<typeof Wolrdbook_branch> {
  return _.has(data, 'folder');
}
function flatten_tree(data: z.infer<typeof Wolrdbook_tree>): z.infer<typeof Wolrdbook_leaf>[] {
  if (is_worldbook_branch(data)) {
    return data.entries.flatMap(flatten_tree);
  }
  return [data];
}
const Wolrdbook_trees = z.array(Wolrdbook_tree).transform(data => data.flatMap(flatten_tree));

export type Worldbook = z.infer<typeof Worldbook>;
export const Worldbook = z.strictObject({
  anchors: z.any().optional().describe(' YAML , '),
  entries: Wolrdbook_trees,
});
